$(document).ready(async function () {
  const sliderB_config = {
    margin: 0,
    loop: false,
    autoplay: false,
    items: 2,
    responsive: {
      0: {
        items: 1
      },
      600: {
        items: 1
      },
      1000: {
        items: 2
      }
    }
  }
  new Owl(sliderB_config).init('#sliderB-1')

  const owlC = new Owl({
    animateOut: 'fadeOut',
    mouseDrag: false,
    touchDrag: false,
    responsive: {
      0: {
        items: 1
      },
      600: {
        items: 1
      },
      1000: {
        items: 1
      }
    }
  })
  owlC.init('#sliderC-1')

  const owlD = new Owl({
    loop: false,
    autoplay: false,
    autoWidth: true,
    margin: 20,
    nav: true,
    responsive: {
      0: {
        items: 1
      },
      600: {
        items: 2
      },
      1000: {
        items: 2
      }
    }
  })
  owlD.init('#sliderD-1')
  $('#sliderD-1 .sliderD__info').append($('#sliderD-1 .owl-nav'))
  $('#sliderD-1 .owl-carousel').append($('#sliderD-1 .more-btn'))

})
