import './text-autospace'
const moreBtn = $('.more-btn'),
  subArticle = $('.article-content--sub')

moreBtn.click(function () {
  $(this).toggleClass('active');
  subArticle.toggleClass('active');
  if ($(this).hasClass('active')) {
    $(this).text('Hide')
  }
  else {
    $(this).text('Read More')
    $('html, body').animate({
      scrollTop: 0
    }, 300)
  }
});
